:root {
  --body-bg: #12192B;
  --body-font-size: 16px;
  --content-width: 1100px;
  --content-width-mode: 1280px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-width-mode: calc(var(--content-width-mode) + (var(--container-offset) * 2));
  --header-height: 62px;
  --scrollbar-thumb: #6866F6;
  --scrollbar-thumb-hover: #f6866F6ff;
  --scrollbar-track: #05031d;
  --scrollbar-track-hover: #05031d;
  --overlay-bg: rgba(0, 0, 0, 0.5);
  --select-inner-bg: #404040;
  --select-inner-border: #FDFDFD;
  --select-focus-border: #FDFDFD;
  --font-family: "Codec Pro" , sans-serif;
  --font-family2: "Gilroy" , sans-serif;
  --cl-a: 255, 255, 255;
  --cl-b: 0, 0, 0;
  --cl-c: 16, 23, 41;
  --cl-d: 148, 163, 184;
  --cl-e: 30, 111, 246;
  --cl-f: 100, 116, 139;
  --cl-g: 22, 31, 53;
  --cl-k: 23, 39, 70;
  --cl-l: 46, 84, 149;
  --bg-a: 255, 255, 255;
  --bg-b: 0, 0, 0;
  --bg-c: 16, 23, 41;
  --bg-d: 148, 163, 184;
  --bg-e: 30, 111, 246;
  --bg-f: 100, 116, 139;
  --bg-g: 22, 31, 53;
  --bg-k: 23, 39, 70;
  --bg-l: 46, 84, 149;
  --br-a: #3D3E5C;
  --br-b: #262D3D;
  --br-c: #6866F6;
  --br-d: #212B62;
  --br-e: #293874;
  --br-f: #546DB3;
  --br-g: #66F6A8;
  --gradient-a:linear-gradient(90deg, #1E2346 0%, #312783 100%);
  --gradient-b:linear-gradient(0deg, rgba(16,23,41,0.36226126192664565) 0%, rgba(16,23,41,0.995314483215161) 86%);
  --gradient-c: linear-gradient(90deg, #FF5D00 -23.81%, #FFB900 74.11%, #FFFB00 144.05%);
  --gradient-d: linear-gradient(90deg, #FF75FF 0%, #2F2DB1 89.23%);
  --gradient-e: linear-gradient(90deg, #FF5D00 33.02%, #FFFB00 106.45%);
