.custom-icon {
  display: flex;
  width: 20px;
  height: 20px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  pointer-events: none;
}
.custom-icon--star {
  width: 40px;
  height: 20px;
  background: var(--star-icon);
}
.custom-icon--stars {
  width: 30px;
  height: 30px;
  position: absolute;
  overflow: hidden;
}
.custom-icon--stars::before {
  content: "";
  height: 1000px;
  background: var(--stars-icon);
  background-repeat: repeat;
  position: absolute;
  width: 100%;
  z-index: 1;
  animation: stars 80s linear infinite;
}
.custom-icon--stars::after {
  position: absolute;
  content: "";
  top: -2px;
  z-index: 2;
  width: 100%;
  height: 7px;
  background: var(--gradient-b);
}
.custom-icon--nav {
  background-image: var(--custom-stars-nav);
  width: 92px;
  height: 50px;
}

.logo {
  padding-right: 20px;
  border-right: 1px solid var(--br-b);
}

.earn-planets {
  border-radius: 90px;
  background-image: url(../img/sprite/earn-bg2.svg), var(--gradient-a);
  padding: 10px 20px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 100%;
  color: rgba(var(--cl-d), 1);
}
.earn-planets .custom-icon {
  position: absolute;
  right: 1px;
  top: -5px;
}

.blue-btn {
  background: rgba(var(--bg-e), 1);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 4px;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  text-transform: capitalize;
  color: rgba(var(--cl-a), 1);
}
.blue-btn svg {
  fill: transparent;
}

.more-btn {
  width: 40px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  border: 1px solid var(--br-a);
  border-radius: 12px;
  color: rgba(var(--cl-a), 1);
}
.more-btn svg {
  fill: currentColor;
  transition: inherit;
}

.read-more {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 100%;
  color: rgba(var(--cl-f), 1);
  transition: color 0.4s;
  display: flex;
  align-items: center;
  grid-gap: 4px;
}
.read-more svg {
  fill: transparent;
  stroke: currentColor;
}

.click-triger {
  display: flex;
  align-items: center;
  grid-gap: 8px;
  justify-content: center;
  width: fit-content;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 112%;
  color: rgba(var(--cl-f), 1);
}
.click-triger svg {
  fill: transparent;
}

.blue-text {
  color: rgba(var(--cl-e), 1);
}

.main-top {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 10px;
}
.main-top P + * {
  margin-top: 10px;
}
.main-top__title {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 100%;
  text-transform: capitalize;
  color: rgba(var(--cl-a), 1);
  position: relative;
}
.main-top p {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: rgba(var(--cl-f), 1);
}
.main-top__btn {
  width: 220px;
  height: 48px;
}

.main-nav__list {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  grid-gap: 20px;
}
.main-nav__link {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 100%;
  color: rgba(var(--cl-d), 1);
  display: flex;
  align-items: center;
  grid-gap: 5px;
  position: relative;
  transition: 0.2s linear;
}
.main-nav__link .custom-icon {
  position: absolute;
}
.main-nav__link .custom-icon--star {
  bottom: 60%;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0.4;
  transition: inherit;
}
.main-nav__link svg {
  fill: transparent;
  stroke: currentColor;
}
.main-nav__link:hover {
  color: rgba(var(--cl-a), 1);
}
.main-nav__link:hover .custom-icon {
  opacity: 1;
}

.hero-info {
  display: grid;
  grid-template-columns: repeat(2, auto);
  max-width: 400px;
  grid-gap: 40px;
}
.hero-info__item {
  display: grid;
