}
.program-button.purpure {
  background: var(--gradient-d);
}
.program-button.purpure::before {
  background-image: var(--custome-gradient-a);
}
.program-button.red {
  background: var(--gradient-e);
}
.program-button.red::before {
  background-image: var(--custome-gradient-b);
}
.program-button__icon {
  position: absolute;
  right: 0;
  top: 0%;
  z-index: -1;
}
.program-button__descr {
  position: absolute;
  max-width: 130px;
  width: 100vw;
  min-height: 30px;
  text-align: center;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, calc(100% + 10px));
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 114%;
  color: rgba(var(--cl-f), 1);
}
.program-button__stars {
