.program-slider__nav {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  position: relative;
  z-index: 1;
}
.program-slider__nav::before {
  width: 100%;
  height: 6px;
  background: rgba(var(--bg-k), 1);
  border-radius: 4px;
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  z-index: -1;
}
.program-slider__nav .swiper-pagination-bullet {
  user-select: none;
  pointer-events: none;
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3;
}
.program-slider__nav .swiper-pagination-bullet::after {
  transition: right 2s linear;
  position: absolute;
  height: 6px;
  top: 50%;
  border-radius: 4px;
  background-size: cover;
  background-position: left;
  background-repeat: no-repeat;
  content: "";
  transform: translateY(-50%) translateX(0%);
  z-index: -1;
  background-image: var(--progress-bar);
}
.program-slider__nav .swiper-pagination-bullet:nth-child(1) .program-button__icon {
  top: -20px;
  right: -9px;
}
.program-slider__nav .swiper-pagination-bullet:nth-child(1)::after {
  left: 0;
  right: calc(min(100vw, 1100px) / 3 - 50%);
  background-image: var(--gradient-f);
}
.program-slider__nav .swiper-pagination-bullet:nth-child(2) {
  z-index: 2;
}
.program-slider__nav .swiper-pagination-bullet:nth-child(2) .program-button__icon {
  top: -25px;
  right: -9px;
}
.program-slider__nav .swiper-pagination-bullet:nth-child(2)::after {
  left: -100%;
  right: calc(min(100vw, 1100px) / 3 + 100%);
}
.program-slider__nav .swiper-pagination-bullet:nth-child(2).swiper-pagination-bullet-active:after {
  right: calc(min(100vw, 1100px) / 3 - 50%);
}
.program-slider__nav .swiper-pagination-bullet:nth-child(2).swiper-pagination-bullet-active + .swiper-pagination-bullet::after {
  right: 150%;
}
.program-slider__nav .swiper-pagination-bullet:nth-child(3) {
  z-index: 1;
}
.program-slider__nav .swiper-pagination-bullet:nth-child(3) .program-button__icon {
  top: -18px;
  right: -8px;
}
.program-slider__nav .swiper-pagination-bullet:nth-child(3)::after {
  left: -200%;
  right: calc(min(100vw, 1100px) / 3 + 150%);
}
.program-slider__nav .swiper-pagination-bullet:nth-child(3).swiper-pagination-bullet-active:after {
  right: 0;
}

