}
.game__list::before {
  position: absolute;
  width: 100%;
  height: 100%;
  content: "";
  filter: blur(2.5px);
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  background-image: var(--bg-image);
  border-radius: inherit;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.game__item {
  position: relative;
  z-index: 2;
  width: 44px;
  height: 44px;
  display: block;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 12px;
  transition: all 0.4s;
}
.game__item:hover {
  filter: drop-shadow(0px 4px 10px rgb(56, 97, 248));
  transform: scale(1.1);
}
.game__item:hover::after {
  opacity: 1;
}
.game__item:hover::before {
  filter: blur(2px);
}
.game__item.active {
  filter: drop-shadow(0px 4px 15px rgba(0, 0, 0, 0));
  transform: scale(1.1);
}
.game__item.active::after {
  opacity: 0;
  transform: scaleX(-1);
}
.game__item.active::before {
  filter: blur(0px);
  transform: scaleX(-1);
}
.game__item.active span {
  box-shadow: 0px 0px 8px 2px rgb(62, 158, 254);
  transform: scaleX(1);
  z-index: 2;
  background: rgba(var(--bg-e), 1);
  font-size: 16px;
}
.game__item::before {
  filter: blur(0px);
  position: absolute;
  width: 100%;
  height: 100%;
  content: "";
  background-image: var(--gradient-g);
  border-radius: inherit;
  border: 1px solid rgba(var(--bg-l), 1);
  transition: inherit;
}
.game__item::after {
  width: 16px;
  height: 33px;
  position: absolute;
  content: "";
  background-image: url(../img/game/item-logo.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  opacity: 0.7;
  transition: inherit;
}
.game__item span {
  pointer-events: none;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(var(--cl-a), 1);
  font-weight: 700;
  font-family: var(--font-family2);
  border-radius: inherit;
  transform: scaleX(-1);
  transition: inherit;
  font-size: 0;
}

